
import {mapState} from 'vuex';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name:'topiclist',
props:['name'],
components:{
Group,
Cell,
Scroller,
Tab,
TabItem
},
data(){
return{
lists:[],

page:1,

limit:15,

pullupConfig:{
content:'',
downContent:'',
upContent:'',
loadingContent:''
},

pullDownConfig:{
content:'',
downContent:'',
upContent:' ',
loadingContent:'...'
}
}
},
computed:{
...mapState(['topics'])
},
methods:{

freshData(){
var app=this
app.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(res=>{
if(res.data&&res.data.success){
app.lists=res.data.data;

app.$refs.pullup.donePulldown()
}
})
},

getDecByTab(tab){
return this.$store.getters.getTopicDescByTab(tab)
},

routeto(index){








this.$router.push('/topiclist/'+this.topics[index].name);

},

loadMoreData(){

var app=this;
this.page=this.page+1


this.$http.get('/topics',{

params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
if(response.data.success){

for(var i=0;i<response.data.data.length;i++){
app.lists.push(response.data.data[i])
}


app.$refs.pullup.donePullup();
}
});




}
},


beforeRouteEnter(to,from,next){






next(function(vm){

vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(response){



if(response.data.success){
vm.lists= response.data.data;



}

})
})

},

beforeRouteUpdate(to,from,next){

var app=this;


this.$http.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
app.lists = response.data.data;
}
});

next()
}
}



