
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
name:'msg',
data(){
return {
selectedTabIndex:0,
hasReadMsgs:null,
hasNotReadMsgs:null
}
},
components:{
ButtonTab,
ButtonTabItem,
Group,
Cell,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
methods:{

markall(){

var app=this;
app.$http.post('/message/mark_all/',{
accesstoken:this.$store.state.accesstoken
}).then(res=>{
if(res.data&&res.data.success){

console.log('');


app.$router.go(0)

}
})

},

markRead(mid){

var app=this;
app.$http.post('/message/mark_one/'+mid,{
accesstoken:this.$store.state.accesstoken
}).then(res=>{
if(res.data&&res.data.success){

console.log('');


app.$router.go(0)
}
})

}
},
created(){

var app=this;
this.$http.get('/messages',{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(res=>{

if(res.data&&res.data.success){
app.hasReadMsgs=res.data.data.has_read_messages;
app.hasNotReadMsgs=res.data.data.hasnot_read_messages;
}

})
}
}



