//
import Vue from  'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

//
Vue.use(Vuex);

//Store
const store =new Vuex.Store({
    //
    state:{
        //
      topics:[
        { name : 'all', desc : ''},
        { name : 'good', desc : ''},
        { name : 'share', desc : ''},
        { name : 'ask', desc : ''},
        { name : 'job', desc : ''},
        { name : 'dev', desc : ''}
      ],
      //
      UnLoginedMoreMenus:{
        login:''
      },
      //
      LoginedMoreMenus:{
        creat:'',
        center:'',
        msg:'',
        logout:''
      },
      //
      loginname:'',
      //
      isLogined:false,
      //
      isShowMoreMenus:false,
      //accesstoken
      accesstoken:'',
      //id
      author_id:''
    },
    //
    getters:{
      moreMenus(state){
        if(state.isLogined){
          //
          return state.LoginedMoreMenus
        }
        return state.UnLoginedMoreMenus
      },
      getTopicDescByTab(state){
        //
        return function(tab){
          for(var index=0;index<state.topics.length;index++){
            if(state.topics[index].name == tab){
              return state.topics[index].desc;
            }
          }
        }
      }
    },
    mutations,
    actions
});

// 
export default store;


